import { NextResponse } from "next/server";
import { db } from "@/lib/prisma";

export async function GET() {
  try {
    // Fetch portfolio size history with model information
    const portfolioData = await db.portfolioSize.findMany({
      include: {
        model: {
          select: {
            name: true,
            openRoutermodelName: true,
          },
        },
      },
      orderBy: {
        createdAt: "asc",
      },
    });

    const serialised = portfolioData.map((entry) => ({
      ...entry,
      createdAt: entry.createdAt.toISOString(),
      updatedAt: entry.updatedAt.toISOString(),
    }));

    return NextResponse.json(serialised);
  } catch (error) {
    console.error("Failed to fetch portfolio history:", error);
    return NextResponse.json(
      {
        error:
          error instanceof Error
            ? error.message
            : "Unknown error while fetching portfolio history",
      },
      { status: 500 }
    );
  }
}
